<template>
  <div class="huiji">
    <!-- 顶部盒子 -->
    <div class="huiji-top">
      <!-- logo -->
      <div class="huiji-top-logo">
        <img src="./static/logo.png" />
        <div>
          <p>数字徽记</p>
          <p>四川徽记研发出品</p>
        </div>
      </div>
      <!-- 标题 -->
      <div class="title">快乐生活 效率徽记</div>
      <!-- 副标题 -->
      <div class="subtitle">
        通过数字徽记，你可以随时随地处理待办，完成工作任务。
      </div>
    </div>
    <!-- 中间内容 -->
    <div class="huiji-cont">
      <div>
        <div>下载数字徽记客户端</div>
        <div>支持苹果iOS和安卓Android</div>
      </div>
      <div>
        <img src="./static/图书备份@3x.png" alt="" width="15px" height="15px" />
        阅读<span style="color: #ffcc62" @click="goPDF">安装指引</span>
      </div>
    </div>
    <!-- 底部盒子 -->
    <div class="but">
      <div class="but-cent">
        <div>
          <div />
          <a :href="url"><img src="./static/编组3备份@3x.png" alt=""/></a>
        </div>
        <div>
          <div />
          <a href=""
            ><img
              src="./static/矩形备份2@3x.png"
              alt=""
              width="100px"
              height="45px"
          /></a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  components: {},
  data() {
    return {
      url: ""
    };
  },
  created() {},
  mounted() {
    this.iosAdd();
  },
  activated: function() {
    this.iosAdd();
  },
  methods: {
    goPDF() {
      this.$router.push({
        path: "pdf"
      });
    },

    iosAdd() {
      this.$api.GetAppAdd().then(res => {
        this.url = res.data.url;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
// 公共样式
.huiji {
  width: 100vw;
  height: 100%;
  &-top {
    width: 100%;
    height: 50%;
    background: url("./static/编组2@3x.png") no-repeat;
    background-size: 100% 100%;
    padding: 41px;
    &-logo {
      width: 24rem;
      height: 12rem;
      display: flex;
      align-items: center;
      img {
        width: 8rem;
        height: 8rem;
        margin-right: 40px;
      }
      > div {
        text-align: left;
        font-weight: bold;
        p:nth-child(1) {
          font-size: 28px;
        }
        p:nth-child(2) {
          font-size: 14px;
        }
      }
    }

    .title {
      font-weight: bold;
      font-size: 28px;
      margin-top: 3rem;
    }
    .subtitle {
      font-weight: bold;
      font-size: 22px;
      margin-top: 3rem;
    }
  }
}

// 移动端
@media (max-width: 414px) {
  .huiji {
    width: 100%;
    height: 743px;
    color: #000;
    font-size: 14px;
    &-top {
      &-logo {
        height: 7rem;
        margin: 0 auto;
      }
      .title {
        margin-top: 3rem;
      }
      .subtitle {
        margin-top: 3rem;
      }
    }

    &-cont {
      margin-top: 3rem;
      padding: 0 41px;
      > div:nth-child(1) {
        > div:nth-child(1) {
          margin-right: 5px;
          font-size: 24px;
          margin: 20px;
        }
        > div:nth-child(2) {
          font-size: 18px;
          margin: 20px;
        }
      }
      > div:nth-child(2) {
        font-size: 18px;
        margin: 20px;
      }
    }

    .but {
      width: 100%;
      padding-top: 2rem;
      &-cent {
        width: 22rem;
        height: 8rem;
        margin: 0 auto;
        // border: 1px solid yellowgreen;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-self: flex-start;
        img {
          width: 120px;
          height: 45px;
        }
      }
    }
  }
} /*<=414的 移动端*/
//  PC端
@media (min-width: 415px) {
  .huiji {
    width: 100%;
    height: 100%;
    color: #000;
    font-size: 24px;
    &-top {
      &-logo {
        height: 7rem;
      }
      .title {
        margin-top: 4rem;
      }
      .subtitle {
        margin-top: 4rem;
      }
    }
    &-cont {
      padding: 0 41px;
      margin-top: 3rem;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      > div:nth-child(1) {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-self: flex-start;
        > div {
          margin-right: 5px;
        }
      }
    }

    .but {
      width: 100%;
      height: auto;
      padding-top: 140px;
      &-cent {
        width: 600px;
        height: 14rem;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-self: flex-start;
        img {
          width: 120px;
          height: 45px;
        }
        > div {
          padding-top: 30px;
          > div {
            margin-bottom: 10px;
          }
          // 两个二维码
          > div:nth-child(1) {
            width: 120px;
            height: 120px;
            background: #ccc !important;
          }
          > div:nth-child(2) {
            width: 120px;
            height: 120px;
            background: #ccc !important;
          }
        }
      }
    }
  }
} /*>=415的设备*/
</style>



